import React, { Component } from 'react';
import withMedia from '../../containers/withmedia';
import {Link} from 'react-router-dom';
import style from './style.css';
import Home from '../../components/homescreen';
import AddGuest from '../../components/addguest';
import ChangeRestaurant from '../../components/destination';

const whichLayout = (layout) => {
    if      (layout === "add-guest")        {   return <AddGuest/> } 
    else if (layout === "home")  {   return <Home /> } 
    else if (layout === "change-restaurant")  {   return <ChangeRestaurant /> } 
  else                                {   return <div/> }
}
class Page extends Component {
    render() {
        const is_large = this.props.browser.greaterThan.small
        const layout = whichLayout(this.props.layout)
        return (
            <div className={style.container} >
              <div className={ is_large ? `container no-padding` : `` }>
                <h1><Link to={`/`}>LunchTime</Link></h1>
                { layout }
              </div>
            </div>
        );
    }
}

export default withMedia(Page)



