const SET_RESTAURANT = "SET_RESTAURANT";
const RENAME_RESTAURANT = "RENAME_RESTAURANT";
const ADD_GUEST = "ADD_GUEST";
const CHOOSE_LOCATION = "CHOOSE_LOCATION";

const UPDATE_SCROLL = "UPDATE_SCROLL";

export const set_restaurant = (payload) => ({ type: SET_RESTAURANT, payload: payload });
export const rename_restaurant = (payload) => ({ type: RENAME_RESTAURANT, payload: payload });
export const add_guest = (payload) => ({ type: ADD_GUEST, payload: payload });
export const choose_location = (payload) => ({ type: CHOOSE_LOCATION, payload: payload });
export const updateScroll = (payload) => ({ type: UPDATE_SCROLL, payload: payload });

let initial = {
  restaurant: null,
  guests: null,
  choose_location: false,
  scroll: { scrollDirection: null, is_bottom: false, is_top: true },
};

export const appReducer = (state, action) => {
    state = state==null ? initial : state
    switch (action.type) {
        case SET_RESTAURANT:
          return {...state, restaurant: Object.assign({}, state.restaurant || {}, action.payload) }
        case RENAME_RESTAURANT:
            return {...state, restaurant: {...state.restaurant, name: action.payload } }
        case ADD_GUEST:
          return {...state, guests: state.guests ? [...state.guests, action.payload] : [action.payload] }
        case CHOOSE_LOCATION:
            return {...state, choose_location: action.payload }
        case UPDATE_SCROLL:
            return {...state, scroll: action.payload }
        default:
            return state;
  }
};



