import React, { PropTypes, Component } from 'react'
import Helmet from "react-helmet";
import { withScriptjs, withGoogleMap, GoogleMap, Marker } from "react-google-maps";
import {withRouter} from 'react-router-dom';
import {connect} from 'react-redux';
import Location from 'react-icons/lib/md/place'
import Edit from 'react-icons/lib/md/edit'
import PlacesAutocomplete, { geocodeByAddress, geocodeByPlaceId, getLatLng } from 'react-places-autocomplete'
import {set_restaurant} from '../../store/reducers';
import MapWithRestaurant from './map.js';

import style from './style.css'

//const Marker = ({ name }) => <div className={ style.destination } ><Location/> { name }</div> ;

const defaultStyles = {
  root: {
    position: 'relative',
    paddingBottom: '0px',
  },
  input: {
    display: 'inline-block',
    width: '100%',
    padding: '10px',
  },
  autocompleteContainer: {
    position: 'absolute',
    top: '100%',
    backgroundColor: 'white',
    border: '1px solid #555555',
    width: '100%',
  },
  autocompleteItem: {
    backgroundColor: '#ffffff',
    padding: '10px',
    color: '#555555',
    cursor: 'pointer',
  },
  autocompleteItemActive: {
    backgroundColor: '#fafafa'
  },
  googleLogoContainer: {
    textAlign: 'right',
    padding: '1px',
    backgroundColor: '#fafafa'
  },
  googleLogoImage: {
    width: 150
  }
}

class Destination extends Component {
    static defaultProps = {
        center: {lat: "48.8566", lng: "2.3522"},
        zoom: 7
    };

  constructor(props){
    super(props)
    this.state = { address: 'Restaurant', latlng: null, name: null, placeID: null, restaurant: null }
    this.onChange = (address) => this.setState({ address, placeID: null, latlng: null, restaurant: null })
    this.saveAddress = (address, placeID) => this.setState({ address, placeID })
    this.getPlace = this.getPlace.bind(this)
    this.saveLocation = (latlng) => this.setState({...this.state, latlng: latlng})
    this.setRestaurant = () => this.setState({...this.state, restaurant: !this.state.restaurant})
    this.saveRestaurant = this.saveRestaurant.bind(this);
  }
  getPlace(address, placeID) {
    this.saveAddress(address, placeID)
    geocodeByAddress(address)
      .then(results => getLatLng(results[0]) )
      .then(latlng => this.saveLocation(latlng) )
      .catch(error => console.error('Error', error))
  }
  saveRestaurant(){
    this.props.set_restaurant({address: this.state.address, lat: this.state.latlng.lat, lng: this.state.latlng.lng })    
    this.props.history.push("/lunchtime/")
  }
  render() {
    const {lat, lng} = this.state.latlng || {}
    const inputProps = {
      value: this.state.address,
      onChange: this.onChange
    }
    //const markers = [this.state].map(({name, latlng}) => <Marker key={`marker_${name}`} lat={ latlng.lat } lng={ latlng.lng } name={ name} /> )
    return (
      <div className={ style.container }>
        <div className={ style.info } >
          <h3 className={ style.title }>Choose Restaurant:</h3> 
          <div>
            <div style={{ position: 'relative;'}}>
              <PlacesAutocomplete inputProps={inputProps} onSelect={this.getPlace} styles={ defaultStyles }/>
            </div>
            { this.state.latlng && !this.state.restaurant &&
            <div>
              <MapWithRestaurant
                loadingElement={<div style={{ height: `100%` }} />}
                containerElement={<div style={{ height: `200px` }} />}
                mapElement={<div style={{ height: `100%` }} />}
                location={ this.state.latlng }
                markers={ <Marker position={ {lat: parseFloat(lat), lng: parseFloat(lng)} } /> }
              />
              <div className={ [style.btn, style.padding].join(" ") } onClick={() => { this.saveRestaurant() }}><span>Set Restaurant</span></div>
            </div>
            }
          </div>
        </div>
      </div>
    )
  }
}

export default withRouter(connect( 
    state =>({
    }),
    dispatch => { 
      return { 
          set_restaurant: (payload) => { dispatch(set_restaurant(payload)) } ,
      } 
    }
)(Destination));




