import React, { Component } from 'react'
import Helmet from "react-helmet";
import { toggleOverlay, updateScroll } from '../../store/reducers';
// import { CSSTransitionGroup } from 'react-transition-group'
import { Switch, Route, withRouter } from 'react-router-dom'
import {connect} from 'react-redux';
//import AsyncRoute from '../../lib/asyncroute';
import withTracker from '../../lib/tracker';

import Page from '../../routes/page';

const NotFoundPage = () => {
    return(
        <div>Page Not Found</div>
    )
}


// const Room = AsyncRoute('room')
// const Resort = AsyncRoute('resort')
//const Page = AsyncRoute('page')


class MasterLayout extends Component {
  
  static scrollPositions = {};

    constructor(props){
        super(props);
        this.state = { scrollDirection: null, is_bottom: false, is_top: true };
        this.lastScrollPosition = 0
        this.scrollDirection = 0
        this.handleScroll = this.handleScroll.bind(this);

        this.currentPage = this.props.location.pathname;
    }
    handleScroll(event) {//{{{
        const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
        const body = document.body;
        const html = document.documentElement;
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight,  html.scrollHeight, html.offsetHeight);
        const windowBottom = windowHeight + window.pageYOffset;
        let newState = null
        let current_scroll_pos = 0

        windowBottom >= docHeight && !(this.props.scroll.is_bottom) && ( newState = { is_bottom: true } )
        windowBottom < docHeight && this.props.scroll.is_bottom && ( newState = { is_bottom: false } )
        
        window.pageYOffset < 100 && !(this.props.scroll.is_top) && ( newState = { is_top: true } )
        window.pageYOffset > 100 && this.props.scroll.is_top && ( newState = { is_top: false } )
        
        current_scroll_pos = window.pageYOffset
        if(this.lastScrollPosition > current_scroll_pos) {
            this.scrollDirection = 'top'
            this.lastScrollPosition = current_scroll_pos
        }
        if(this.lastScrollPosition < current_scroll_pos) {
            this.scrollDirection = 'bottom'
            this.lastScrollPosition = current_scroll_pos
        }
        ( this.props.scroll.scrollDirection !== this.scrollDirection ) && ( newState = Object.assign(newState || {},{ scrollDirection: this.scrollDirection }))


        if(newState) { this.props._update_scroll(newState) }
    }//}}}
    componentDidMount() {//{{{
        window.addEventListener("scroll", this.handleScroll);
        // console.log("Posts Did Mount")
    }//}}}
    componentWillUnmount() {//{{{
        window.removeEventListener("scroll", this.handleScroll);
        // console.log("Posts Will unMount")
    }//}}}

    //Restore Scroll Position
    componentWillReceiveProps(nextProps) {//{{{
        // console.log("I am MasterLayout I am receiving props");
        // console.log("MasterLayout:componentWillReceiveProps: this pathname " + JSON.stringify(this.props.location.pathname))
        // console.log("MasterLayout:componentWillReceiveProps: next pathname " + JSON.stringify(nextProps.location.pathname))
        if(nextProps.location.pathname !== this.props.location.pathname){
            //Save current window scrolling position
            const winHeight = window.scrollY; //window.innerHeight;
            MasterLayout.scrollPositions[this.props.location.pathname] = winHeight;
            // console.log("MasterLayout: Page Scroll Postions:" + JSON.stringify(MasterLayout.scrollPositions))

        }
    }//}}}
    componentDidUpdate(prevProps, prevState) {//{{{
        // console.log("MasterLayout: ComponentDidUpdate fired!")
        // console.log("MasterLayout:componentdidupdate: this pathname " + JSON.stringify(this.props.location.pathname))
        // console.log("MasterLayout:componentdidupdate: prev pathname " + JSON.stringify(prevProps.location.pathname))
        if(this.props.location.pathname !== prevProps.location.pathname){
            // console.log("I am MasterLayout I am updating and have a new location");
            let scrollToTop = this.props.location.hash.length === 0;
            //Restore position
            const pos = MasterLayout.scrollPositions[this.props.location.pathname];
            // console.log(`MasterLayout: ${this.props.history.action} Action in ${this.props.location.pathname} = ${pos}`);
            if (this.props.history.action === 'POP') {
                this.props._toggle_overlay(false);
                if (pos) {
                    window.scroll(0, parseInt(pos));
                    scrollToTop = false;
                }
            }
            if (scrollToTop) {
            // Scroll to top of viewport
                window.scroll(0, 0);
            }
            
        }
    }//}}}

    render() {
        const { location } = this.props
        const greater_than = this.props.browser.greaterThan
        const greater_thans = Object.keys(greater_than).filter((i) => { return greater_than[i] } ).map((i) => { return(`is_gt_${i}`) }).join(" ")
        const less_than = this.props.browser.lessThan
        const less_thans = Object.keys(less_than).filter((i) => { return less_than[i] } ).map((i) => { return(`is_lt_${i}`) }).join(" ")
        return (
            <div>
                <Helmet>
                    <body className={`is_${ this.props.browser.orientation } ${ less_thans } ${ greater_thans } is_${this.props.browser.mediaType}  direction${this.props.scroll.scrollDirection} ${this.props.scroll && this.props.scroll.is_top ? 'is_top' : 'not_top' }`} />
                </Helmet>
                <Switch key={location.key} location={location}>
                  <Route path="/lunchtime" exact render={ (props) => <Page  layout="home" { ...props } /> } />
                  <Route path="/lunchtime/change-restaurant" exact render={ (props) => <Page  layout="change-restaurant" { ...props } /> } />
                  <Route path="/lunchtime/add-guest" exact render={ (props) => <Page  layout="add-guest" { ...props } /> } />
                  <Route component={NotFoundPage} />
                </Switch>
            </div>
        )
    }
}

export default withRouter(withTracker(connect( 
    state =>({
        browser: state.browser,
        scroll: state.app.scroll,
    }),
    dispatch => { 
        return { 
            _update_scroll: (payload) => { dispatch(updateScroll(payload))}
        } 
    }
)(MasterLayout)));




