const API_URL = "https://www.sunrise-resorts.com/graphql"
const LOCAL_URL = "http://127.0.0.1:5555/graphql"
export const getGraph = ({operationName, payload, variables=null}) => {
    return new Promise(function(resolve, reject) {
        let request=new XMLHttpRequest();
        let jwt = localStorage.jwt
        request.open("POST",  process.env.NODE_ENV == 'production' ? "/graphql" : "http://127.0.0.1:5555/graphql", true);
        request.setRequestHeader("Content-Type", "application/json");
        request.setRequestHeader("Accept","application/json");
        request.setRequestHeader("Content-Type","application/json");
        String(jwt)!=='undefined' && request.setRequestHeader("Authorization",`Bearer ${jwt}`);
        request.setRequestHeader("credentials",'include') 
        request.send(JSON.stringify({"query":payload, "variables": variables, "operationName": operationName}));
        request.onreadystatechange = () => {
            if (request.readyState === 4) {
            resolve(request.responseText)
            }
        }
    })
}



