import React, { Component } from 'react';
import { BrowserRouter as Router } from 'react-router-dom'
import { Provider } from 'react-redux';
import Helmet from "react-helmet";
import MasterLayout from './masterlayout';
import store from '../store';

import '../../node_modules/getbase/dist/css/styles.css';
import '../assets/style.css';
import '../style/index.css';


export default class App extends Component {
    render() {
      const apikey = ""
      const local_host = "http://127.0.0.1:3000"
      const host_name = window.location.hostname == '127.0.0.1' 
          ? local_host 
          : process.env.NODE_ENV === 'production' 
              ? "https://ibrahim.github.io/lunchtime" 
              : local_host
      return (
          <div id="app">
              <Helmet
                  htmlAttributes={{lang: "en", amp: undefined}} 
                  defaultTitle="Lunchtime"
                  titleAttributes={{itemprop: "name", lang: "en"}}
                  base={{target: "_blank", href: host_name}}
                  onChangeClientState={(newState) => console.log(newState) } >
                      <meta content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=0" name="viewport" />
                      <meta name="description" content="Lunchtime"/>
                      <meta property="og:type" content="Website"/>
                      <meta name="apple-mobile-web-app-capable" content="yes"/>
                      <meta name="apple-mobile-web-app-title" content="Lunchtime" />
                      <meta name="apple-mobile-web-app-status-bar-style" content="translucent" />
                      <link rel="canonical" href="https://www.sunrise-resorts.com/app"/>
                      <link rel="apple-touch-icon" sizes="144x144" href="/apple-icon-144x144.png" />
                      <link rel="apple-touch-icon" sizes="114x114" href="/apple-icon-114x114.png" />
                      <link rel="apple-touch-icon" sizes="72x72" href="/apple-icon-72x72.png"/>
                      <link rel="apple-touch-icon" sizes="57x57" href="/apple-icon-57x57.png"/>
                      <link rel="apple-touch-startup-image" href="/images/preloader.gif"/>
              </Helmet>
              <Provider store={store()}>
                  <Router>
                      <MasterLayout />
                  </Router>
              </Provider>
          </div>
      );
            }
}



